import PropTypes from 'prop-types';
import { useRef,useState,useEffect } from 'react';
// @mui
import { Grid, Stack } from '@mui/material';
//
import ProfileAbout from './ProfileAbout'; 
import PorfileChannelInput from './ProfileChannelInput'; 
import ProfileFollowInfo from './ProfileFollowInfo';
import ProfileSocialInfo from './ProfileSocialInfo';
import {useDispatch,useSelector,store} from '../../../../redux/store';
import {setChannel,loadChannel}  from '../../../../redux/slices/channel';
// ----------------------------------------------------------------------

Profile.propTypes = {
  myProfile: PropTypes.object,
  posts: PropTypes.array,
};

export default function Profile({ myProfile, posts }) {
  
  const dispatch = useDispatch();
 
  const handleSave = (param)=>{

    dispatch(setChannel(param));
  } 
  useEffect(() => {
    dispatch(loadChannel());
  }, [dispatch]);
 

  return (
    <Grid container spacing={3}>
      <Grid item xs={12} md={6}>
        <Stack spacing={3}>
          <PorfileChannelInput handleSave = {handleSave}/>
          
        </Stack>
      </Grid>
      <Grid item xs={12} md={6}>
        <Stack spacing={3}>
          <ProfileFollowInfo profile={myProfile} />
          <ProfileAbout profile={myProfile} />
          <ProfileSocialInfo profile={myProfile} />
        </Stack>
      </Grid>
    </Grid>
  );
}
