import { useRef,useState,useEffect } from 'react';

// @mui
import { FormControlLabel, Card, Button, TextField, CardHeader,Stack, Typography, Divider,Switch  } from '@mui/material';
import PropTypes from 'prop-types';

// components
import Iconify from '../../../../components/Iconify';
// utils
import { fNumber } from '../../../../utils/formatNumber';

import {setChannel,loadChannel}  from '../../../../redux/slices/channel';
